<template>
  <div class="board-box" :style="{width: canvasWidth + 'px', height: canvasHeight + 'px'}">
    <ul class="tool-box">
      <li v-for="item in tools" :key="item.type" class="tool-con" :class="{active: toolType === item.type}" @click="changeTool(item.type)">
        <i class="iconfont" :class="item.icon"></i>
      </li>
    </ul>
    <canvas id="myCanvas" ref="myCanvas"></canvas>
  </div>
</template>

<script>
import { fabric } from 'fabric'

let defaultProps = {
  canvasWidth: {
    type: Number,
    default: window.innerWidth
  },
  canvasHeight: {
    type: Number,
    default: window.innerHeight
  },
  canvasBgColor: {
    type: String,
    default: '#f6f6f6'
  },
  strokeColor: {
    type: String,
    default: '#f30'
  },
  strokeWidth: {
    type: Number,
    default: 2
  },
  showTools: {
    type: Boolean,
    default: true
  }
}

const TOOLS = [
  {
    name: '',
    type: 'pencil',
    icon: 'icon-pan_icon'
  },
  {
    name: '',
    type: 'line',
    icon: 'icon-xian'
  },
  {
    name: '',
    type: 'dashedLine',
    icon: 'icon-xian1'
  },
  {
    name: '',
    type: 'arrow',
    icon: 'icon-jiantou'
  },
  {
    name: '',
    type: 'text',
    icon: 'icon-wenzi'
  },
  {
    name: '',
    type: 'rect',
    icon: 'icon-juxing'
  },
  {
    name: '',
    type: 'cricle',
    icon: 'icon-yuanxing'
  },
  {
    name: '',
    type: 'ellipse',
    icon: 'icon-tuoyuanxing'
  },
  {
    name: '',
    type: 'equilateral',
    icon: 'icon-sanjiaoxing'
  },
  {
    name: '',
    type: 'rubber',
    icon: 'icon-xiangpi'
  },
  {
    name: '',
    type: 'undo',
    icon: 'icon-undo'
  },
  {
    name: '',
    type: 'redo',
    icon: 'icon-redo'
  },
  {
    name: '',
    type: 'clear',
    icon: 'icon-clear'
  },
  {
    name: '',
    type: 'move',
    icon: 'icon-yidong'
  }

]

export default {
  name: 'vue-fabric-sketchpad',
  data () {
    return {
      boardObj: null, // 
      mouseFrom: {}, // 
      mouseTo: {}, // 
      toolType: 'pencil', // 
      isDrawing: false, //  
      drawingObj: null, // 
      history: [], // 
      textObj: null, // 
      stage: 0,
      tools: TOOLS
    }
  },
  props: defaultProps,
  mounted () {
    this.boardObj = new fabric.Canvas('myCanvas', {
      isDrawingMode: true, // 
      selectable: true, // 
      selection: true, // 
      devicePixelRatio: true
    })
    this.boardObj.freeDrawingBrush.color = this.strokeColor // 
    this.boardObj.freeDrawingBrush.width = this.strokeWidth // 
    this.boardObj.setWidth(this.canvasWidth)
    this.boardObj.setHeight(this.canvasHeight)
    this.initEvent()
  },
  methods: {
    initEvent () {
      this.boardObj.on({
        'mouse:down': (e) => {
          console.log('down', e)
          let { x, y } = e.pointer
          this.mouseFrom = { x, y }
          // 
          if (!this.boardObj.getActiveObject()) {
            this.isDrawing = true
          }
          if (this.toolType === 'text') {
            this.drawText()
          }
        },
        'mouse:up': (e) => {
          console.log('up', e)
          let { x, y } = e.pointer
          this.mouseTo = { x, y }
          this.isDrawing = false
          this.drawingObj = null
          this.addHistory()
        },
        'mouse:move': e => {
          if (this.isDrawing) {
            console.log('move', e)
            let { x, y } = e.pointer
            this.mouseTo = { x, y }
            this.drawing()
          }
        },
        // 
        'object:moving': e => {
          console.log('', e)
        },
        // 
        'object:scaling': e => {
          console.log('', e)
        },
        // 
        'object:rotating': e => {
          console.log('', e)
        },
        'selection:created': e => {
          console.log('')
          if (this.toolType === 'rubber') {
            if (e.target._objects) {
              // 
              var etCount = e.target._objects.length
              for (var i = 0; i < etCount; i++) {
                this.boardObj.remove(e.target._objects[i])
              }
            } else {
              // 
              this.boardObj.remove(e.target)
            }
            this.boardObj.discardActiveObject() // 
          }
          this.addHistory()
        }
      })
    },
    drawing () {
      //  
      if (this.drawingObj) {
        this.boardObj.remove(this.drawingObj)
      }
      this.boardObj.isDrawingMode = false
      this.boardObj.selectable = false
      this.boardObj.selection = false
      let drawObj = null
      switch (this.toolType) {
        case 'pencil':
          this.boardObj.isDrawingMode = true
          break
        case 'line':
          drawObj = this.drawLine()
          break
        case 'dashedLine':
          drawObj = this.drawDashedLine()
          break
        case 'arrow':
          drawObj = this.drawArrow()
          break
        case 'rect':
          drawObj = this.drawRect()
          break
        case 'cricle':
          drawObj = this.drawCricle()
          break
        case 'ellipse':
          drawObj = this.drawEllipse()
          break
        case 'equilateral':
          drawObj = this.drawEquilateral()
          break
        default:
          drawObj = null
          this.boardObj.selection = true
          this.boardObj.skipTargetFind = false
          this.boardObj.selectable = true
          break
      }
      if (drawObj) {
        console.log(drawObj)
        this.boardObj.add(drawObj)
        this.drawingObj = drawObj
      }
    },
    // 
    changeTool (type) {
      this.boardObj.isDrawingMode = false
      this.resetDrawObj()
      if (type === 'clear') {
        return this.boardObj.clear()
      }
      if (type === 'undo') {
        return this.undo()
      }
      if (type === 'redo') {
        return this.redo()
      }
      this.toolType = type
      if (type === 'pencil') {
        this.boardObj.isDrawingMode = true
      }
      if (type === 'rubber' || type === 'move') {
        this.boardObj.selection = true
        this.boardObj.skipTargetFind = false
        this.boardObj.selectable = true
      }
    },
    resetDrawObj () {
      this.boardObj.selectable = true
      this.boardObj.selection = true
      this.boardObj.skipTargetFind = false
      if (this.textObj) {
        this.textObj.exitEditing()
        this.textObj = null
      }
    },
    // 
    drawLine () {
      return new fabric.Line([this.mouseFrom.x, this.mouseFrom.y, this.mouseTo.x, this.mouseTo.y], {
        stroke: this.strokeColor,
        strokeWidth: this.strokeWidth
      })
    },
    // 
    drawDashedLine () {
      return new fabric.Line(
        [this.mouseFrom.x, this.mouseFrom.y, this.mouseTo.x, this.mouseTo.y],
        {
          stroke: this.strokeColor,
          strokeWidth: this.strokeWidth,
          strokeDashArray: [10, 3]
        }
      )
    },
    // 
    drawRect () {
      return new fabric.Rect({
        left: this.mouseFrom.x,
        top: this.mouseFrom.y,
        height: Math.abs(this.mouseTo.y - this.mouseFrom.y),
        width: Math.abs(this.mouseTo.x - this.mouseFrom.x),
        stroke: this.strokeColor,
        strokeWidth: this.strokeWidth,
        fill: 'rgba(255, 255, 255, 0)'
      })
    },
    // 
    drawCricle () {
      //    a^2 + b^2 = c^2
      let a = this.mouseTo.x - this.mouseFrom.x
      let b = this.mouseTo.y - this.mouseFrom.y
      let r = Math.sqrt(Math.pow(a, 2) + Math.pow(b, 2)) / 2
      return new fabric.Circle({
        left: this.mouseFrom.x,
        top: this.mouseFrom.y,
        stroke: this.strokeColor,
        fill: 'rgba(255, 255, 255, 0)',
        radius: r,
        strokeWidth: this.strokeWidth
      })
    },
    // 
    drawEllipse () {
      let left = this.mouseFrom.x
      let top = this.mouseFrom.y
      return new fabric.Ellipse({
        left: left,
        top: top,
        stroke: this.strokeColor,
        fill: 'rgba(255, 255, 255, 0)',
        originX: 'center',
        originY: 'center',
        rx: Math.abs(left - this.mouseTo.x),
        ry: Math.abs(top - this.mouseTo.y),
        strokeWidth: this.strokeWidth
      })
    },
    // 
    drawEquilateral () {
      let height = this.mouseTo.y - this.mouseFrom.y
      return new fabric.Triangle({
        top: this.mouseFrom.y,
        left: this.mouseFrom.x,
        width: Math.sqrt(Math.pow(height, 2) + Math.pow(height / 2, 2)),
        height: height,
        stroke: this.strokeColor,
        strokeWidth: this.strokeWidth,
        fill: 'rgba(255,255,255,0)'
      })
    },
    // 
    drawArrow () {
      let [sx, sy] = [this.mouseFrom.x, this.mouseFrom.y]
      let [ex, ey] = [this.mouseTo.x, this.mouseTo.y]
      let disY = sy - ey
      let disX = sx - ex
      let theta = 30
      let headlen = 30
      var angle = Math.atan2(disY, disX) * 180 / Math.PI
      var angle1 = (angle + theta) * Math.PI / 180
      var angle2 = (angle - theta) * Math.PI / 180
      var topX = headlen * Math.cos(angle1)
      var topY = headlen * Math.sin(angle1)
      var botX = headlen * Math.cos(angle2)
      var botY = headlen * Math.sin(angle2)
      var arrowX = sx - topX
      var arrowY = sy - topY
      var path = ' M ' + sx + ' ' + sy
      path += ' L ' + ex + ' ' + ey
      arrowX = ex + topX
      arrowY = ey + topY
      path += ' M ' + arrowX + ' ' + arrowY
      path += ' L ' + ex + ' ' + ey
      arrowX = ex + botX
      arrowY = ey + botY
      path += ' L ' + arrowX + ' ' + arrowY
      return new fabric.Path(path, {
        stroke: this.strokeColor,
        fill: 'rgba(255,255,255,0)',
        strokeWidth: this.strokeWidth
      })
    },
    drawText () {
      let { x, y } = this.mouseFrom
      this.textObj = new fabric.Textbox('', {
        left: x,
        top: y,
        width: '',
        height: '',
        fill: this.strokeColor,
        hasControls: true
      })
      this.boardObj.add(this.textObj)
      this.textObj.enterEditing()
      this.textObj.hiddenTextarea.focus()
      this.addHistory()
    },
    // 
    addHistory () {
      this.history.push(JSON.stringify(this.boardObj))
    },
    undo () {
      let stack = this.history
      if (this.stage < stack.length) {
        this.boardObj.clear().renderAll()
        this.boardObj.loadFromJSON(stack[stack.length - 1 - this.stage - 1])
        this.boardObj.renderAll()
        this.stage += 1
      }
    },
    redo () {
      let stack = this.history
      if (this.stage > 0) {
        this.boardObj.clear().renderAll()
        this.boardObj.loadFromJSON(stack[stack.length - 1 - this.stage + 1])
        this.boardObj.renderAll()
        this.stage -= 1
      }
    }
  }
}
</script>

